<template>
  <header class="header">
    <!--start navbar-->
    <nav
        class="navbar navbar-expand-lg fixed-top"
        v-bind:class="{
                affix: hasAffix,
                'custom-nav': coloredLogo,
                'bg-transparent': !coloredLogo,
                'white-bg': coloredLogo,
            }"
    >
      <div class="container">
        <a class="navbar-brand" href="/"
        ><img
            :src="
                            coloredLogo
                                ? 'img/logo-black.png'
                                : 'img/logo-black.png'
                        "
            width="150"
            alt="logo"
            class="img-fluid"
        /></a>

        <button
            class="navbar-toggler"
            type="button"
            @click="mobileNavClicked"
            v-bind:class="{ collapsed: collapsed }"
        >
          <span class="ti-menu"></span>
        </button>

        <li class="collapse navbar-collapse main-menu h-auto" v-bind:class="{ show: !collapsed }"
            id="navbarSupportedContent">
          <ul class="navbar-nav ml-auto">
            <li class="nav-item">
              <router-link class="nav-link page-scroll" to="/" v-scroll-to="'#home'">Home
              </router-link>
            </li>
            <li class="nav-item">
              <router-link class="nav-link page-scroll" to="/#tokenomics" v-scroll-to="'#tokenomics'">Tokenomics
              </router-link>
            </li>
            <li class="nav-item">
              <router-link class="nav-link page-scroll" to="/#roadmap" v-scroll-to="'#roadmap'">Roadmap</router-link>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="docs/litepaper-moomin.pdf" target="_blank">Litepaper</a>
            </li>
          </ul>
        </li>
      </div>
    </nav>
    <!--end navbar-->
  </header>
</template>

<style>
.navbar-brand {
  color: #fff;
  font-size: 48px;
  font-weight: bold;
  font-family: 'Cinzel Decorative', sans-serif;
}
</style>

<script>

export default {
  props: {
    coloredLogo: {
      type: Boolean,
      default: false,
    },
  },
  data: function () {
    return {
      windowTop: 0,
      collapsed: true,
    };
  },
  mounted() {
    window.addEventListener("scroll", this.onScroll);
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.onScroll);
  },
  methods: {
    onScroll: function () {
      this.windowTop = window.top.scrollY;
    },
    mobileNavClicked: function () {
      this.collapsed = !this.collapsed;
    }
  },
  computed: {
    hasAffix: function () {
      return this.windowTop > 0;
    },
  },
};
//test
</script>

